import { useContext } from 'react';
import Card from 'react-bootstrap/Card';
import { Context } from '../App';
import AllianceLogo from '../Images/AllianceLogo.png';

import '../App.css';

function MainView() {

    const App = useContext(Context);

    return (
        <div className="MainView">
            <img src={AllianceLogo} style={{ maxHeight: '20vh' }} />
            <h1 className="site-title">Bloodfang EU</h1>
            <Card className="mainMenu" >
                <Card.Body className="mainMenuCardBody">
                    <input type="text" id="searchInput" onChange={(e) => {
                        App.HandleSearchInput(e.target.value)
                    }} />
                    <input className="searchButton" type="submit" value="Search" onClick={() => {
                        App.GetItemList();
                    }} />
                </Card.Body>
            </Card >
            <div className="result-cards">
                {App.RenderCards()}
            </div>
        </div >
    );
}

export default MainView;