import React, { useEffect, useState, useContext } from 'react';
import { Line } from 'react-chartjs-2';
import { Context } from '../../App';

import './Chart.css'

function Chart() {

    const App = useContext(Context);
    console.log(App)
    const [values, setValues] = useState([]);
    const [date, setDates] = useState([]);
    const [quantity, setQuantity] = useState([]);

    useEffect(() => {
        if (App.SelectedItem.RawPrice !== undefined) {
            for (var i = 0; i < Object.keys(App.SelectedItem.RawPrice).length; i++) {
                if (App.SelectedItem.RawPrice[i] !== undefined) {
                    if (parseInt(App.SelectedItem.RawPrice[i].minBuyout) > 10000) {
                        values.push(App.SelectedItem.RawPrice[i].minBuyout / 10000);
                    }
                    else {
                        values.push(App.SelectedItem.RawPrice[i].minBuyout);
                    }
                }
            }
            setValues([...values]);

            for (var j = 0; j < Object.keys(App.SelectedItem.RawPrice).length; j++) {
                if (App.SelectedItem.RawPrice[j] !== undefined) {
                    quantity.push(App.SelectedItem.RawPrice[j].quantity);
                }
            }
            setQuantity([...quantity]);

            for (var k = 0; k < Object.keys(App.SelectedItem.RawPrice).length; k++) {
                if (App.SelectedItem.RawPrice[k] !== undefined) {
                    var dateTime = new Date(App.SelectedItem.RawPrice[k].scannedAt);
                    date.push(dateTime.getFullYear() + '-' + (dateTime.getMonth() + 1).toString().padStart(2, 0) + '-' + dateTime.getDate() + ' ' + dateTime.getHours().toString().padStart(2, 0) + ':' + dateTime.getMinutes().toString().padStart(2, 0));
                }
            }
            setDates([...date]);
        };
        // eslint-disable-next-line
    }, []);

    const data = {
        labels: date,
        datasets: [
            {
                label: 'Price',
                data: values,
                fill: true,
                backgroundColor: 'rgba(255, 255, 255, 0.5)',
                borderColor: 'rgb(255, 255, 255)',
            },
            {
                label: 'Quantity',
                data: quantity,
                fill: false,
                backgroundColor: 'rgb(45, 181, 196)',
                borderColor: 'rgb(45, 181, 196)',
            },
        ],
    };

    const options = {
        scales: {
            yAxes: [
                {
                    ticks: {
                        beginAtZero: false,
                    },
                },
            ],
        },
    };

    return (
        <>
            <Line className="chart" data={data} options={options} />
        </>
    );
}

export default Chart;