import React, { useState } from 'react';
import { Redirect } from "react-router-dom";
import ItemDetailsCard from './Components/ItemDetailsCard/ItemDetailsCard';
import Chart from './Components/Chart/Chart';

import './ItemView.css';

function ItemView() {

  const [redirect, setRedirect] = useState(false);

  return (
    <>
      {redirect ? <Redirect to="/AuctionSearch" /> : null}
      <div className="ItemView">
        <ItemDetailsCard />
        <Chart />
      </div >
      <input className="backButton" type="submit" value="Back" style={{ width: '10vw', height: '3vw', marginLeft: '8vw', fontSize: '2.5rem' }} onClick={() => { setRedirect(true) }} />
    </>
  );
}

export default ItemView;
