import React, { useEffect, useState, useContext } from 'react';
import { Redirect } from 'react-router';
import Card from 'react-bootstrap/Card';
import axios from 'axios';
import goldCoin from './images/money-gold.gif';
import silverCoin from './images/money-silver.gif';
import copperCoin from './images/money-copper.gif';
import ItemIcon from '../ItemIcon/ItemIcon';
import { Context } from '../../App';

import './ItemCard.css';

function ItemCard(ItemData) {

    const App = useContext(Context);

    //Item States:
    const [Item] = useState(ItemData);
    const [ItemDetails, setItemDetails] = useState(null);
    const [ImageUrl, setImageUrl] = useState(null);
    const [ItemColor, setItemColor] = useState(null);
    const [RawPrice, setRawPrice] = useState(null);
    const [MinBuyoutGold, setMinBuyoutGold] = useState(null);
    const [MinBuyoutSilver, setMinBuyoutSilver] = useState(null);
    const [MinBuyoutCopper, setMinBuyoutCopper] = useState(null);

    const [redirect, setRedirect] = useState(false);

    //Get selected item image:
    const GetImage = (e) => {
        return axios({
            method: 'get',
            url: `https://us.api.blizzard.com/data/wow/media/item/${e}?namespace=static-classic-us&locale=en_US&access_token=USwowWgzaa1HgPEZhEMT5rayMTC7ge5pBm`
        })
            .then(function (response) {
                setImageUrl(response.data.assets[0].value)
            })
            .catch(function (error) {
                console.log(error);
            });
    };
    //Get item quality color:
    const GetColor = (e) => {
        switch (e.data.quality.name.en_GB) {
            case 'Poor':
                setItemColor('grey');
                break;
            case 'Common':
                setItemColor('white');
                break;
            case 'Uncommon':
                setItemColor('#1eff00');
                break;
            case 'Rare':
                setItemColor('#0070dd');
                break;
            case 'Epic':
                setItemColor('#a335ee');
                break;
            case 'Legendary':
                setItemColor('#ff8000');
                break;
            default:
                setItemColor('white');
                break;
        }
    };
    //Get selected item price:
    const GetItemPrice = (e) => {
        return axios({
            method: 'get',
            url: `https://api.nexushub.co/wow-classic/v1/items/bloodfang-alliance/${e}/prices`
        })
            .then(function (response) {
                setRawPrice(response.data.data);
                setMinBuyoutGold(Math.trunc(response.data.data[response.data.data.length - 1].minBuyout / 10000));
                setMinBuyoutSilver(Math.trunc((response.data.data[response.data.data.length - 1].minBuyout % 10000) / 100));
                setMinBuyoutCopper(Math.trunc((response.data.data[response.data.data.length - 1].minBuyout % 10000) % 100));
            })
            .catch(function (error) {
                console.log(error);
            });
    };
    //Specific item search:
    const SearchItem = (e) => {
        return axios({
            method: 'get',
            url: `https://eu.api.blizzard.com/data/wow/item/${e}?namespace=static-classic-eu&locale=en_US&access_token=USwowWgzaa1HgPEZhEMT5rayMTC7ge5pBm`
        })
            .then(function (response) {
                setItemDetails(response.data);
            })
            .catch(function (error) {
                return null;
            });
    };
    const SelectItem = () => {
        ItemDetails.ImageUrl = ImageUrl;
        ItemDetails.ItemColor = ItemColor;
        ItemDetails.MinBuyoutGold = MinBuyoutGold;
        ItemDetails.MinBuyoutSilver = MinBuyoutSilver;
        ItemDetails.MinBuyoutCopper = MinBuyoutCopper;
        ItemDetails.RawPrice = RawPrice;
        App.setSelectedItem(ItemDetails);
        setRedirect(true);
    }

    useEffect(() => {
        SearchItem(Item.data.data.id);
        GetImage(Item.data.data.media.id);
        GetColor(Item.data);
        GetItemPrice(Item.data.data.id);
        // eslint-disable-next-line
    }, []);

    return (
        <div className="item-card" style={{ display: 'flex' }} onClick={() => { SelectItem() }}>
            {redirect ? <Redirect to="/item" /> : null}
            <ItemIcon imageUrl={ImageUrl} />
            {ItemDetails !== null ? <Card style={{ maxWidth: '15rem', margin: '2vh', marginLeft: '5px', minWidth: '15rem' }}>
                <Card.Body>
                    <Card.Title style={{ color: ItemColor }}>{ItemDetails.name}</Card.Title>
                    <Card.Text className="item-level">{'Item Level ' + ItemDetails.level}</Card.Text>
                    <Card.Text className="binding">{ItemDetails.preview_item.binding !== undefined ? ItemDetails.preview_item.binding.name : null}</Card.Text>
                    <Card.Text className="quantity">{ItemDetails.preview_item.unique_equipped !== null ? ItemDetails.preview_item.unique_equipped : null}</Card.Text>
                    <div className="type-div">
                        <Card.Text className="type">{ItemDetails.preview_item.inventory_type.name !== null ? ItemDetails.preview_item.inventory_type.name : null}</Card.Text>
                        <Card.Text className="weapon-type">{ItemDetails.item_subclass.name !== null ? ItemDetails.item_subclass.name : null}</Card.Text>
                    </div>
                    <Card.Text className="useEffect">{ItemDetails.preview_item.spells !== undefined ? ItemDetails.preview_item.spells[0].description : null}</Card.Text>
                    <Card.Text className="sellPrice">
                        {ItemDetails.preview_item.sell_price !== undefined ? 'Sell Price' : null}
                        {ItemDetails.preview_item.sell_price !== undefined && parseInt(ItemDetails.preview_item.sell_price.display_strings.gold) > 0 ? <> {ItemDetails.preview_item.sell_price.display_strings.gold}<img src={goldCoin} alt="" /> </> : null}
                        {ItemDetails.preview_item.sell_price !== undefined && parseInt(ItemDetails.preview_item.sell_price.display_strings.silver) > 0 ? <> {ItemDetails.preview_item.sell_price.display_strings.silver}<img src={silverCoin} alt="" /> </> : null}
                        {ItemDetails.preview_item.sell_price !== undefined && parseInt(ItemDetails.preview_item.sell_price.display_strings.copper) > 0 ? <> {ItemDetails.preview_item.sell_price.display_strings.copper}<img src={copperCoin} alt="" /> </> : null}
                    </Card.Text>
                    {MinBuyoutGold !== null ? <Card.Text className="itemPrice">
                        {ItemDetails.preview_item.sell_price !== undefined ? 'Min Buyout: ' : null}
                        {MinBuyoutGold !== null ? <> {MinBuyoutGold}<img src={goldCoin} alt="" /> </> : null}
                        {MinBuyoutSilver !== null ? <> {MinBuyoutSilver}<img src={silverCoin} alt="" /> </> : null}
                        {MinBuyoutCopper !== null ? <> {MinBuyoutCopper}<img src={copperCoin} alt="" /> </> : null}
                    </Card.Text> : null}
                </Card.Body>
            </Card > : null}
        </div>
    );
}

export default ItemCard;