import React, { useContext } from 'react';
import Card from 'react-bootstrap/Card';
import goldCoin from './images/money-gold.gif';
import silverCoin from './images/money-silver.gif';
import copperCoin from './images/money-copper.gif';
import ItemIcon from '../ItemIcon/ItemIcon';
import { Context } from '../../App';

import './ItemDetailsCard.css';

function ItemDetailsCard() {

    const App = useContext(Context);

    return (
        <div className="item-card" style={{ display: 'flex', height: '100%', zoom: '150%', marginLeft: '2vh' }} >
            <ItemIcon imageUrl={App.SelectedItem.ImageUrl} />
            <Card style={{ maxWidth: '15rem', margin: '2vh', marginLeft: '5px', minWidth: '15rem' }}>
                <Card.Body>
                    <Card.Title style={{ color: App.SelectedItem.ItemColor }}>{App.SelectedItem.name}</Card.Title>
                    <Card.Text className="item-level">{'Item Level ' + App.SelectedItem.level}</Card.Text>
                    <Card.Text className="binding">{App.SelectedItem.preview_item.binding !== undefined ? App.SelectedItem.preview_item.binding.name : null}</Card.Text>
                    <Card.Text className="quantity">{App.SelectedItem.preview_item.unique_equipped !== null ? App.SelectedItem.preview_item.unique_equipped : null}</Card.Text>
                    <div className="type-div">
                        <Card.Text className="type">{App.SelectedItem.preview_item.inventory_type.name !== null ? App.SelectedItem.preview_item.inventory_type.name : null}</Card.Text>
                        <Card.Text className="weapon-type">{App.SelectedItem.item_subclass.name !== null ? App.SelectedItem.item_subclass.name : null}</Card.Text>
                    </div>
                    <Card.Text className="useEffect">{App.SelectedItem.preview_item.spells !== undefined ? App.SelectedItem.preview_item.spells[0].description : null}</Card.Text>
                    <Card.Text className="sellPrice">
                        {App.SelectedItem.preview_item.sell_price !== undefined ? 'Sell Price' : null}
                        {App.SelectedItem.preview_item.sell_price !== undefined && parseInt(App.SelectedItem.preview_item.sell_price.display_strings.gold) > 0 ? <> {App.SelectedItem.preview_item.sell_price.display_strings.gold}<img src={goldCoin} alt="" /> </> : null}
                        {App.SelectedItem.preview_item.sell_price !== undefined && parseInt(App.SelectedItem.preview_item.sell_price.display_strings.silver) > 0 ? <> {App.SelectedItem.preview_item.sell_price.display_strings.silver}<img src={silverCoin} alt="" /> </> : null}
                        {App.SelectedItem.preview_item.sell_price !== undefined && parseInt(App.SelectedItem.preview_item.sell_price.display_strings.copper) > 0 ? <> {App.SelectedItem.preview_item.sell_price.display_strings.copper}<img src={copperCoin} alt="" /> </> : null}
                    </Card.Text>
                    {App.SelectedItem.MinBuyoutGold !== null ? <Card.Text className="itemPrice">
                        {App.SelectedItem.preview_item.sell_price !== undefined ? 'Min Buyout: ' : null}
                        {App.SelectedItem.MinBuyoutGold !== null ? <> {App.SelectedItem.MinBuyoutGold}<img src={goldCoin} alt="" /> </> : null}
                        {App.SelectedItem.MinBuyoutSilver !== null ? <> {App.SelectedItem.MinBuyoutSilver}<img src={silverCoin} alt="" /> </> : null}
                        {App.SelectedItem.MinBuyoutCopper !== null ? <> {App.SelectedItem.MinBuyoutCopper}<img src={copperCoin} alt="" /> </> : null}
                    </Card.Text> : null}
                </Card.Body>
            </Card >
        </div>
    );
}

export default ItemDetailsCard;