import axios from 'axios';
import React, { useEffect, useState } from 'react';
import { BrowserRouter, Route, Switch } from "react-router-dom";
import MainView from './Components/MainView';
import ItemView from './ItemView';
import ItemCard from './Components/ItemCard/ItemCard';

export const Context = React.createContext({});

function App() {

  //Search States:
  const [Results, setResults] = useState([]);
  const [SearchInput, setSearchInput] = useState('');
  const [SelectedItem, setSelectedItem] = useState(null);

  //Get items matching input:
  const GetItemList = () => {
    if (SearchInput.length > 3) {
      return axios({
        validateStatus: false,
        method: 'get',
        url: `https://eu.api.blizzard.com/data/wow/search/item?namespace=static-classic-eu&name.en_GB=${SearchInput}&orderby=id&_page=1&_pageSize=300&access_token=USwowWgzaa1HgPEZhEMT5rayMTC7ge5pBm`

      })
        .then(function (response) {
          if (response.data.results.length > 0) {
            for (var i = 0; i < response.data.results.length; i++) {
              if (response.data.results[i].data.name.en_GB.toUpperCase().includes(SearchInput.toUpperCase())) {
                Results.push(response.data.results[i])
                setResults([...Results]);
              }
            }
          }
        })
        .catch(function (error) {
          console.error(error)
        });
    }
  };
  //Save search input to state:
  const HandleSearchInput = (e) => {
    setSearchInput(e);
  };
  //Render a card component for each search result:
  const RenderCards = () => {
    if (Results !== null) {
      return Results.map((e) => {
        switch (e.data.item_class.name.en_GB) {
          default:
            return <ItemCard key={e.data.id} data={e} />
        }
      });
    }
  };

  useEffect(() => {
    setResults([]);
  }, [SearchInput]);


  return (
    <Context.Provider value={{ GetItemList, RenderCards, HandleSearchInput, setSelectedItem, SelectedItem, setResults, Results }} >
      <BrowserRouter>
        <Switch>
          <Route path="/AuctionSearch">
            <MainView />
          </Route>
          <Route path="/item">
            <ItemView />
          </Route>
        </Switch>
      </BrowserRouter>
    </Context.Provider>
  );
}

export default App;